// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";
body::-webkit-scrollbar {
	display:none;
}

#nav {
	border: solid 2px #20b2aa;
	height: 70px;
	position: fixed;
	width: 100%;
	z-index: 100;

	.nav-field {
		height: 100%;

		.nav-list {
			height: 100%;
			margin: 0;
			padding: 10px;
			width: 100%;

			li {
				display: block;
				float: left;
				font-size: 15pt;
				margin: 0;
				padding: 10px;

				a {
					text-decoration: none;
				}
			}
		}
	}


}

.heading {
	border-left: ridge 10px #202d3b;
	border-bottom: solid 2px #202d3b;
	margin-top: 20px;
	padding: 0;

	h2,h3 {
		margin: 0;
	}
}

.info-list {
	position: relative;

	img {
		position: absolute;
		top: -10px;
		right: -20px;
	}
}

.accessmap {
	height: 450px;
	position: relative;

	.konko {
		background-image: url('/assets/image/konko/item-konko.png');
		background-repeat: no-repeat;
		position: relative;
		height: 46px;
		top: 30px;
		z-index: 5;
		transition: 0.5s ease-in 0s;
	}

	#map {
		height: 450px;
		padding: 0;
		position: absolute;
		top: 0;
		z-index: 10;
	}

	&:hover .konko{
		top: -30px;
	}
}

.thumbnail-img {
	background-color: #ffffff;
	border-radius: 300px;
}

.member {
	margin-top: 20px;
}

.profile {
	margin-top: 20px;
}

#btn-contact {
	margin-top: 20px;
}

#contact {
	background-color: #F9F9F9;

	.contact-form {
		height: 850px;
		width: 100%;
	}
}

.ss-required-asterisk {
	color: #9a2626;
}

.footer {
	background-color: #202d3b;
	color: #ffffff;
	height: 50px;
	padding: 10px;
	text-align: center;
}
